"use client"

import Link from "next/link"
import { useCart } from "@/contexts/CartContext"
import { useState, use } from "react"
import Toast from "@/components/Toast"

interface Product {
  id: string;
  name: string;
  description: string;
  price: number;
  image: string;
  details?: string;
}

interface Products {
  [key: string]: Product;
}

// This will be replaced with actual data fetching later
const products: Products = {
  "essential-journal": {
    id: "essential-journal",
    name: "Essential Journal",
    description: "Minimalist journal for your daily thoughts and ideas.",
    price: 24.99,
    image: "https://images.pexels.com/photos/733857/pexels-photo-733857.jpeg",
    details: `
      <p>
        Our Essential Journal is crafted with simplicity and functionality in mind. Perfect for daily journaling, sketching, or note-taking, this minimalist journal features:
      </p>
      
      <ul class="list-disc pl-6 my-4">
        <li>192 pages of premium 100gsm paper</li>
        <li>Lay-flat binding for comfortable writing</li>
        <li>Durable linen cover</li>
        <li>Inner pocket for loose papers</li>
        <li>Elastic closure band</li>
        <li>Ribbon bookmark</li>
      </ul>
      
      <p>
        Dimensions: 5.8" × 8.3" (A5)
      </p>
    `
  },
  "minimal-desk-lamp": {
    id: "minimal-desk-lamp",
    name: "Minimal Desk Lamp",
    description: "Clean and functional lighting for your workspace.",
    price: 89.99,
    image: "https://images.pexels.com/photos/1112598/pexels-photo-1112598.jpeg",
    details: `
      <p>
        The Minimal Desk Lamp combines form and function in a sleek design that complements any workspace. Features include:
      </p>
      
      <ul class="list-disc pl-6 my-4">
        <li>Adjustable arm and head for precise lighting</li>
        <li>Touch-sensitive dimming control</li>
        <li>Energy-efficient LED bulb included</li>
        <li>Built-in USB charging port</li>
        <li>Available in matte black or white</li>
      </ul>
    `
  },
  "ceramic-mug": {
    id: "ceramic-mug",
    name: "Ceramic Mug",
    description: "Handcrafted ceramic mug with minimalist design.",
    price: 19.99,
    image: "https://images.pexels.com/photos/1566308/pexels-photo-1566308.jpeg",
    details: `
      <p>
        Each Ceramic Mug is handcrafted by skilled artisans, making every piece unique. The mug features:
      </p>
      
      <ul class="list-disc pl-6 my-4">
        <li>300ml capacity</li>
        <li>Durable stoneware construction</li>
        <li>Minimalist matte finish</li>
        <li>Comfortable handle design</li>
        <li>Microwave and dishwasher safe</li>
      </ul>
    `
  }
}

export default function ProductPage({ params }: { params: { id: string } }) {
  const { addItem } = useCart()
  const [showToast, setShowToast] = useState(false)
  
  const id = use(Promise.resolve(params.id))
  const product = products[id]

  const handleAddToCart = () => {
    addItem({
      id: product.id,
      name: product.name,
      price: product.price,
      image: product.image
    })
    setShowToast(true)
  }

  if (!product) {
    return (
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-16">
        <h1 className="text-4xl font-bold">Product not found</h1>
        <Link href="/shop" className="mt-4 inline-block text-gray-600 hover:text-black">
          ← Back to shop
        </Link>
      </div>
    )
  }

  return (
    <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
      {/* Product Navigation */}
      <div className="py-8">
        <Link href="/shop" className="text-gray-600 hover:text-black">
          ← Back to shop
        </Link>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-2 gap-x-12 gap-y-8 pb-24">
        {/* Product Image */}
        <div className="aspect-square overflow-hidden rounded-lg bg-gray-100">
          <img
            src={product.image}
            alt={product.name}
            className="h-full w-full object-cover object-center"
          />
        </div>

        {/* Product Info */}
        <div className="flex flex-col">
          <h1 className="text-3xl font-bold">{product.name}</h1>
          <p className="mt-4 text-xl">${product.price.toFixed(2)}</p>
          <p className="mt-4 text-gray-600">{product.description}</p>
          
          <div className="mt-8 prose prose-gray max-w-none">
            <h2 className="text-xl font-semibold mb-4">Details</h2>
            <ul className="space-y-2">
              <li>Free shipping on orders over $50</li>
              <li>30-day return policy</li>
              <li>Handcrafted with care</li>
              <li>Sustainable materials</li>
            </ul>
          </div>

          <button 
            onClick={handleAddToCart}
            className="mt-8 w-full bg-black text-white py-4 px-4 rounded-md hover:bg-gray-800 transition-colors"
          >
            Add to Cart
          </button>
        </div>
      </div>

      {/* Toast Notification */}
      {showToast && (
        <Toast 
          message="Item added to cart"
          onClose={() => setShowToast(false)}
        />
      )}
    </div>
  )
}
